<template>
  <modal class="provider-modal" title="My Account" width="580px" cancelText="Sign out" :cancel="signOut">
    <div class="account-list">
      <ul class="harvest">
        <li v-for="item in $s.bnp.harvest" :key="item.id">
          <i class="icon"><img :src="item.ico" /></i>
          <span class="num">{{item.num | balanceNum}}</span>
          <span class="help" @click="viewon()">View on Etherscan</span>
        </li>
      </ul>
    </div>
  </modal>
</template>
<script>
import config from '@/config'
export default {
  name: 'modal-account',
  mounted() {
    this.$s.bnp.fetchAllTokenBalanceOf()
  },
  methods: {
    viewon() {
      const account = this.$s.account.address
      let baseUrl = `https://etherscan.io/address/`
      switch(config.networkID) {
        case 3:
          baseUrl = 'https://ropsten.etherscan.io';
          break
      }
      const url = `${baseUrl}/address/${account}#tokentxns`
      window.open(url)
    },
    signOut() {
      this.$s.resetConnect()
    }
  }
};
</script>
<style lang="less" scoped>
.account-list {
  margin: 0 auto;
  background: #fafafa;
  width: 90%;
  padding: 30px 5%;
  box-sizing: border-box;
}
.harvest {
  .num {
    font-size: 38px;
  }
  .icon, .help {
    vertical-align: top;
  }
  .help {
    cursor: pointer;
  }
}
</style>